"use client";

import { useEffect, useState } from "react";
import { useRouter } from "next/navigation";
import { useNotionConnection } from "@/hooks/useNotionConnection";
import { ConnectButton } from "@/components/notion/ConnectButton";
import { LoadingSpinner } from "@/components/ui/LoadingSpinner";

export default function HomePage() {
  const router = useRouter();
  const { state } = useNotionConnection();
  const [mounted, setMounted] = useState(false);

  useEffect(() => {
    setMounted(true);
  }, []);

  if (!mounted || state === "loading") {
    return (
      <main className="min-h-screen flex items-center justify-center">
        <LoadingSpinner size="lg" />
      </main>
    );
  }

  return (
    <main className="min-h-screen flex flex-col items-center justify-center px-6">
      <div className="max-w-md w-full flex flex-col items-center text-center gap-8">
        {/* Logo */}
        <div className="flex items-center gap-3 mb-2">
          <img src="/logo.png" alt="App Logo" className="w-8 h-8" />
          <span className="text-lg font-semibold tracking-tight">
            Notion MCP
          </span>
        </div>

        {/* Heading */}
        <div className="flex flex-col gap-3">
          <h1 className="text-3xl font-bold tracking-tight">
            Connect your Notion workspace
          </h1>
          <p className="text-neutral-500 text-sm leading-relaxed">
            Link your Notion account to search pages, query databases, and
            interact with your workspace through MCP.
          </p>
        </div>

        {/* CTA */}
        {state === "connected" ? (
          <button
            onClick={() => router.push("/dashboard")}
            className="bg-white text-black font-semibold text-base px-8 py-4 rounded-2xl hover:bg-neutral-100 active:scale-[0.98] transition-all duration-200 shadow-lg shadow-white/5"
          >
            Go to Dashboard →
          </button>
        ) : (
          <ConnectButton />
        )}

        {/* Footer hint */}
        <p className="text-[11px] text-neutral-700 max-w-xs">
          Your credentials are encrypted and stored server-side. We never access
          your Notion data without your permission.
        </p>
      </div>
    </main>
  );
}
